%clear-horizontal-spacing {
    @media (max-width: $desktop){
        margin-left: calc(var(--full-width-padding)* -1) !important;
        margin-right: calc(var(--full-width-padding)* -1) !important;
    }
}

.gform_button {
    border-radius: 9999px !important;
    font-weight: 700 !important;
}

.has-pointer {
    a {
        text-decoration: none;
        color: $primary;
        position: relative;
        transition: color 0.3s ease-in-out, border-bottom 0.3s ease-in-out;
        border-bottom: 1px solid transparent; // Invisible border to prevent layout shift
        &:hover {
            color: $secondary;
            border-bottom-color: $secondary; // Add white underline
            .fa-chevron-right {
                transform: translateX(10px); /* Moves icon 10px to the right */
            }
        }
        .fa-chevron-right {
            position: absolute;
            left: 100%;
            top: 2.5px;
            transition: transform 0.3s ease-in-out;
            margin-left: 5px; /* Adds spacing between text and icon */
        }
    }
}

.wp-block-button:not(.is-style-outline) {
    > a {
        transition: background-color 0.3s ease, border-color 0.3s ease, color 0.3s ease, border-radius 0.3s ease;
        &:hover {
            background-color: $secondary;
            border-color: $secondary;
            border-radius: 0;
        }
    }
}

.wp-block-button.is-style-outline {
    .has-primary-color {
        transition: color 0.3s ease, border-radius 0.3s ease;
        &:hover {
            // border-color: $secondary;
            color: $secondary !important;
            border-radius: 0;
        }
    }
}

.is-layout-constrained > .alignwide {
    &.shrink {
        &__below_desktop {
            @media (max-width: $desktop){
                max-width: var(--wp--style--global--content-size);
            }
        }
    }
}

.mw-600 {
    max-width: 600px !important;
}

figure {
    margin-bottom: 0;
}

.d-none {
    display: none !important;
}

.d-tablet-none {
    @media screen and (min-width: $tablet){
