.wp-block-button__link {
    border-radius: 9999px;
}


html {
    max-width: 100vw;
    overflow-x: hidden;
    body {
        max-width: 100vw;
    }
}

.wp-block-post-featured-image {
    margin-bottom: 0;
}


.wp-site-blocks {
    position: relative;
    .entry-content {
        --full-width-padding: 1.25rem;
        padding-left: var(--full-width-padding);
        padding-right: var(--full-width-padding);

        .full-width, .alignfull {
            box-sizing: border-box;
            max-width: unset;
            width: 100vw;
            margin-left: calc(var(--full-width-padding) * -1) !important;
            margin-right: calc(var(--full-width-padding) * -1) !important;
            padding-left: var(--full-width-padding);
            padding-right: var(--full-width-padding);
        }
        
        ul {
            li {
                a {
                    color: var(--wp--preset--color--primary);
                    &:hover {
                        color: var(--wp--preset--color--secondary);
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 768px) {
    .wp-site-blocks {
        .entry-content {
            --full-width-padding: 2.5rem;
        }
    }
}

@media screen and (min-width: 1440px) {
    .wp-site-blocks {
        .entry-content {
            --full-width-padding: 5rem;
        }   
    }
}

@media screen and (min-width: 1920px) {
    html {
        font-size: calc(16px * (100vw / 1920px));
    }
    .wp-site-blocks {
        .entry-content {
            --full-width-padding: 10rem;
        }   
    }
}

@media screen and (min-width: 2560px) {
    img {
        min-width: 100%;
    }
}


.video-banner-wrap {
    padding: 0 !important;
}

.video-banner-content {
    max-width: 458px;
    padding: 3rem;
    background-color: rgba(#fff, 0.7);
}

.video-banner {
    position: relative;
    width: 100vw;
    height: 180px; /* Full-screen height */
    overflow: hidden;
    margin-right: 0 !important;
    margin-left: 0 !important;
    max-width: unset;
}

.video-background {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100vw;
    height: 400px;
    transform: translate(-50%, -50%);
    pointer-events: none; /* Disables all user interactions */
}

$videoHeight: 540px;
$videoWrapperHeight: 1000px;
$estimatedTopPosition: 70px;
$estimatedTopPositionDesktop: 100px; 
$videoHeightDesktop: 600px;
$videoWrapperHeightDesktop: 1500px;

@media screen and (min-width: $tabletSmall){
    .video-banner {
        height: 400px;
    }

    .video-background {
        height: 768px;
    }
}

@media screen and (min-width: $tablet){
    
    #video-banner-content-wrapper {
        position: absolute;
        top: $estimatedTopPosition;
        left: 100px;
        z-index: 5;
    }
    .video-banner {
        height: $videoHeight;
    }

    .video-background {
        height: $videoWrapperHeight;
    }
}

@media screen and (min-width: $desktop){
    #video-banner-content-wrapper {
        top: $estimatedTopPositionDesktop;
    }
    .video-banner {
        height: $videoHeightDesktop;
    }

    .video-background {
        height: $videoWrapperHeightDesktop;
    }
}

/* Transparent overlay to block hover & click interactions */
.video-overlay {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    background: transparent; /* Blocks hover actions */
    z-index: 2;
    pointer-events: auto;
}


.contained-banner {
    // padding-left: 2rem;
    // padding-right: 2rem;
    &.wp-block-cover {
        min-height: 220px;
    }
    
    .wp-block-cover__inner-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        .wp-block-buttons {
            justify-content: center;
        }
        h2 {
            text-align: center;
        }
        @media screen and (min-width: $tablet){
            .wp-block-buttons {
                justify-content: flex-start;
            }
            h2 {
                text-align: left;
                padding-right: 0;
            }
            align-items: flex-start;
        }
    }
}

body:not(.block-editor-page){
    .card-hover {
        $root: &;
        width: 360px;
        height: 500px;
        position: relative;
        overflow: hidden;
        box-shadow: 0 0 32px -10px rgba(0,0,0,0.08);
      
        &:hover {
            .card-content {
                background-color: $tertiary;
                bottom: 100%;
                transform: translateY(100%);
                padding: 50px 60px;
                transition: all 0.35s cubic-bezier(.1,.72,.4,.97);
                color: $secondary;
            }
      
            a {
              opacity: 1;
            //   transform: translate(-50%, 0);
              transition: opacity 0.3s 0.35s cubic-bezier(.1,.72,.4,.97);
            }
        }
      
        .card-content {
          text-align: center;
          background-color: $tertiary;
          color: $secondary;
          @media screen and (min-width: $desktop){
            color: #fff;
            background-color: $secondary;
          }
          position: absolute;
          bottom: 0;
          left: 0;
          transform: translateY(0);
          transition: all 0.35s 0.35s cubic-bezier(.1,.72,.4,.97);
          will-change: bottom, background-color, transform;
          z-index: 1;
      
          &::before,
          &::after {
            content: '';
            width: 100%;
            height: 120px;
            background-color: inherit;
            position: absolute;
            left: 0;
            z-index: -1;
          }
      
          &::before {
            top: -80px;
            clip-path: ellipse(60% 80px at bottom center);
          }
      
          &::after {
            bottom: -80px;
            clip-path: ellipse(60% 80px at top center);
          }
        }
      
        h3 {
          font-size: 1.5rem;
          margin-bottom: 1em;
      
        }
        a {
          @media screen and (min-width: $desktop){
            opacity: 0;
          }
        }
      
          i {
            width: 18px;
            margin-left: 4px;
            transition: transform 0.3s;
          }
        }
      
        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
      }

.page-id-25 h4.get-a-quote {
    display: none;
}

h4.get-a-quote {
    position: fixed;
    z-index: 99;
    right: 2rem;
    bottom: 2rem;
    border-radius: 50%;
    @media screen and (min-width: $tablet){
        width: 120px;
        height: 120px;
    }
    .has-extra-large-font-size {
        font-size: 2rem;
    }
    &.has-background {
        padding: .5rem;
    }
    a {
        text-decoration: none;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}


.two-col-layout {
    figure {
        margin-bottom: 0;
    }
    .wp-block-columns {
        align-items: center !important;
    }
}

.swap-order {
    &__tablet {
        @media screen and (min-width: $tablet){
            .wp-block-column {
                &:first-child {
                    order: 2
                }
                &:last-child {
                    order: 1;
                }
            }
        }
    }
    &__desktop {
        @media screen and (min-width: $desktop){
            .wp-block-column {
                &:first-child {
                    order: 2
                }
                &:last-child {
                    order: 1;
                }
            }
        }
    }
}

.the-clear-choice {
    &.wp-block-columns {
        flex-direction: column;
        @media screen and (min-width: $desktop) {
            flex-direction: row;
        }
        .wp-block-lg-blocks-font-awesome {
            display: flex;
            justify-content: center;
            align-items: center;
            i {
                font-size: 10rem;
            }
        }
    }
}

.image-slider {
    position: relative;
    $overlayWidth: 300px;
    &__overlay {
        position: absolute;
        left: calc(50% - $overlayWidth/2);
        bottom: 2.5rem;
        z-index: 10;
        width: $overlayWidth;
    }
    .wp-block-lg-blocks-advanced-slider {
        min-height: 400px;
        img {
            height: 600px;
        }
        // .swiper-wrapper {
        //     height: 400px !important;
        // }
        .main {
            .swiper-button-prev {
                // right: 3rem;
                padding: 1rem;
                border-radius: 50%;
                color: white !important;
            }
            .swiper-button-next {
                right: 1rem;
                padding: 1rem;
                border-radius: 50%;
                color: white !important;
            }
        }
    }
}

.wp-block-lg-blocks-accordion {
    > .accordion-container {
        border-bottom: 1px solid $tertiary;
        &:first-child {
            border-top: 2px solid $tertiary;
        }
        .accordion-header {
            padding: 1rem 0;
            margin-bottom: 0;
            &.collapsed {
                &::after {
                    content: "\f068";
                }
            }
            color: $primary;
            cursor:  pointer;
            &::after {
                font: var(--fa-font-solid);
                content: "\2b";
                font-size: 2rem;
                right: 0;
            }
        }
        .accordion-collapse {
            padding: 0;
            .accordion-body {
                p {
                    margin: 0;
                }
            }
        }
    }
}

